export const dialogDoc = "<?xml version=\"1.0\" encoding=\"utf-8\" ?>\n<!DOCTYPE html PUBLIC \"-//W3C//DTD XHTML 1.1//EN\" \"http://www.w3.org/TR/xhtml11/DTD/xhtml11.dtd\">\n<html xmlns=\"http://www.w3.org/1999/xhtml\">\n	<head>\n		<meta http-equiv=\"Content-Type\" content=\"text/html;charset=utf-8\" />\n		<meta http-equiv=\"Referrer-Policy\" content=\"no-referrer\" />\n		<meta name=\"referrer\" content=\"no-referrer\" />\n		<meta name=\"viewport\" content=\"width=device-width,initial-scale=1,minimum-scale=1\" />\n		<meta name=\"robots\" content=\"noindex,nofollow\" />\n		<base href=\"https://webalert.pages.dev/\" />\n		<title>Dialog</title>\n		<style type=\"text/css\">\n@import url(\"res/ubuntu/ubuntu.css\");\n\n* {\n	margin: 0px;\n	padding: 0px;\n}\n\n*:hover, *:active {\n	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n}\n\n::selection {\n	color: #ffffff;\n	background-color: #006060;\n}\n\ninput, select, textarea {\n	font-size: inherit;\n	font-family: inherit;\n	font-weight: inherit;\n	font-style: inherit;\n	color: inherit;\n	background-color: rgba(0, 0, 0, 0);\n}\n\nbody {\n	position: absolute;\n	display: block;\n	width: 100%;\n	height: 100%;\n	font-size: 14px;\n	font-weight: 500;\n	font-family: \"Ubuntu\";\n	font-style: normal;\n	color: #323232;\n	accent-color: #006060;\n	background-color: rgba(0, 0, 0, 0.5);\n	overflow: hidden;\n}\n\n#dialog {\n	position: absolute;\n	display: block;\n	width: 250px;\n	height: fit-content;\n	padding: 20px;\n	max-height: 100%;\n	overflow-x: hidden;\n	overflow-y: auto;\n	border-style: solid;\n	border-width: 1px;\n	border-color: #000000;\n	border-radius: 10px;\n	background-color: #ffffff;\n	visibility: hidden;\n}\n\n#titlebar {\n	position: relative;\n	display: block;\n	width: 100%;\n	height: 30px;\n	color: #303030;\n	background-color: rgba(0, 0, 0, 0.1);\n}\n\n#titlebar-icon {\n	position: absolute;\n	display: block;\n	width: 20px;\n	height: 20px;\n	left: 5px;\n	top: 5px;\n}\n\n#titlebar-title {\n	position: absolute;\n	display: block;\n	width: calc(100% - 90px);\n	height: 20px;\n	left: 10px;\n	top: 5px;\n	cursor: default;\n	line-height: 20px;\n	text-align: start;\n	font-size: 16px;\n	text-overflow: ellipsis;\n	overflow: hidden;\n	overflow-wrap: break-word;\n}\n\n.titlebar-button {\n	position: absolute;\n	display: block;\n	width: 20px;\n	height: 20px;\n	top: 5px;\n	cursor: pointer;\n	font-size: 16px;\n	font-weight: 600;\n	line-height: 20px;\n	text-align: center;\n	-webkit-user-select: none;\n	user-select: none;\n	color: #000000;\n	background-color: #a0a0a0;\n	border-width: 1px;\n	border-style: solid;\n	border-top-color: #ffffff;\n	border-left-color: #ffffff;\n	border-right-color: #000000;\n	border-bottom-color: #000000;\n}\n\n.titlebar-button[disabled=\"true\"] {\n	color: #808080;	\n}\n\n.titlebar-button:active {\n	border-top-color: #000000;\n	border-left-color: #000000;\n	border-right-color: #ffffff;\n	border-bottom-color: #ffffff;\n}\n\n.titlebar-button[disabled=\"true\"]:active {\n	border-top-color: #ffffff;\n	border-left-color: #ffffff;\n	border-right-color: #000000;\n	border-bottom-color: #000000;\n}\n\n#titlebar-close-button {\n	right: 5px;\n}\n\n#titlebar-maximize-button {\n	right: 30px;\n}\n\n#titlebar-minimize-button {\n	right: 55px;\n}\n\n.frame {\n	position: relative;\n	display: block;\n	width: 100%;\n	height: calc(100% - 30px);\n	border: none;\n	overflow: hidden;\n}\n\n#element-container {\n	position: relative;\n	display: block;\n	width: 100%;\n	height: fit-content;\n	margin-bottom: 10px;\n}\n\n.title {\n	position: relative;\n	display: block;\n	width: 100%;\n	height: fit-content;\n	margin-top: 5px;\n	margin-bottom: 5px;\n	line-height: 24px;\n	font-size: 18px;\n	font-weight: 600;\n	text-align: center;\n}\n\n.message {\n	position: relative;\n	display: block;\n	width: 100%;\n	height: fit-content;\n	line-height: 20px;\n	text-align: start;\n	overflow-x: hidden;\n	overflow-wrap: break-word;\n}\n\n.input {\n	position: relative;\n	display: block;\n	width: calc(100% - 10px);\n	height: 16px;\n	line-height: 16px;\n	margin-top: 10px;\n	margin-bottom: 10px;\n	padding: 5px;\n	outline: none;\n	accent-color: #006060;\n	border-style: solid;\n	border-width: 1px;\n	border-color: #808080;\n	border-radius: 10px;\n}\n\n.input:focus {\n	border-color: #303030;\n}\n\n#button-bar {\n	position: relative;\n	display: flex;\n	width: fit-content;\n	height: fit-content;\n	margin: auto;\n	gap: 10px;\n	flex-direction: row;\n	flex-wrap: nowrap;\n}\n\n.button {\n	position: relative;\n	display: block;\n	width: fit-content;\n	height: fit-content;\n	padding: 10px;\n	color: #ffffff;\n	background-color: #006060;\n	cursor: pointer;\n	-webkit-user-select: none;\n	user-select: none;\n	border-width: 1px;\n	border-style: solid;\n	border-radius: 10px;\n	border-top-color: #ffffff;\n	border-left-color: #ffffff;\n	border-right-color: #000000;\n	border-bottom-color: #000000;\n}\n\n.button:active {\n	border-top-color: #000000;\n	border-left-color: #000000;\n	border-right-color: #ffffff;\n	border-bottom-color: #ffffff;\n}\n\n.button[disabled=true] {\n	background-color: #808080;\n}\n\n.button[disabled=true]:active {\n	border-top-color: #ffffff;\n	border-left-color: #ffffff;\n	border-right-color: #000000;\n	border-bottom-color: #000000;\n}\n		</style>\n	</head>\n	<body>\n		<div id=\"dialog\"></div>\n		<script type=\"text/javascript\">\n\"use strict\";\n\n// <![CDATA[\n(() => {\n\ndocument.oncontextmenu = (e) => {\n	e.preventDefault();\n	e.stopPropagation();\n};\n\ndocument.onkeydown = (e) => {\n	function checkKey() {\n		const ctrl = e.ctrlKey || e.metaKey;\n		const shift = e.shiftKey;\n		const code = e.keyCode;\n\n		if (ctrl) {\n			if (shift) {\n				switch (code) {\n					case 73: // ctrl+shift+i\n					case 74: // ctrl+shift+j\n						return true;\n				}\n			}\n\n			switch (code) {\n				case 83: // ctrl+s\n				case 85: // ctrl+u\n					return true;\n			}\n		}\n\n		switch (code) {\n			case 123: // f12\n				return true;\n			default:\n				return false;\n		}\n	}\n\n	if (checkKey()) {\n		e.preventDefault();\n		e.stopPropagation();\n		return false;\n	}\n\n	return true;\n};\n\n})();\n// ]]>\n		</script>\n	</body>\n</html>";
export const shellDoc = "<?xml version=\"1.0\" encoding=\"utf-8\" ?>\n<!DOCTYPE html PUBLIC \"-//W3C//DTD XHTML 1.1//EN\" \"http://www.w3.org/TR/xhtml11/DTD/xhtml11.dtd\">\n<html xmlns=\"http://www.w3.org/1999/xhtml\">\n	<head>\n		<meta http-equiv=\"Content-Type\" content=\"text/html;charset=utf-8\" />\n		<meta http-equiv=\"Referrer-Policy\" content=\"no-referrer\" />\n		<meta name=\"referrer\" content=\"no-referrer\" />\n		<meta name=\"viewport\" content=\"width=device-width,initial-scale=1,minimum-scale=1\" />\n		<meta name=\"robots\" content=\"noindex,nofollow\" />\n		<base href=\"https://webalert.pages.dev/\" />\n		<title>Debug Shell</title>\n		<script type=\"text/javascript\" src=\"res/brython.js\"></script>\n		<script type=\"text/javascript\" src=\"res/brython_stdlib.js\"></script>\n		<style type=\"text/css\">\n@import url(\"res/ubuntu/ubuntu.css\");\n\n* {\n	margin: 0px;\n	padding: 0px;\n}\n\n*:hover, *:active {\n	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n}\n\ninput, select, textarea {\n	font-size: inherit;\n	font-family: inherit;\n	font-weight: inherit;\n	font-style: inherit;\n	color: inherit;\n	background-color: rgba(0, 0, 0, 0);\n}\n\nbody {\n	position: absolute;\n	display: block;\n	width: 100%;\n	height: 100%;\n	font-size: 14px;\n	font-weight: 500;\n	font-family: \"Ubuntu Mono\";\n	font-style: normal;\n	color: #323232;\n	background-color: #ffffff;\n	overflow: hidden;\n}\n\n#console {\n	position: absolute;\n	display: block;\n	width: calc(100% - 20px);\n	height: calc(100% - 20px);\n	padding: 10px;\n	border: none;\n	resize: none;\n	outline: none;\n}\n		</style>\n	</head>\n	<body>\n		<textarea id=\"console\" rows=\"20\" cols=\"80\" spellcheck=\"false\">Loading...</textarea>\n		<script type=\"text/python\">\nfrom interpreter import Interpreter;\n\nit = Interpreter(\"console\");\nit.feed(\"from browser import window as win; window = win.top; document = window.document; console = window.console;\");\n		</script>\n		<script type=\"text/javascript\">\n\"use strict\";\n\n// <![CDATA[\n(() => {\n\nconst brython = window.brython;\nif (typeof brython != \"function\") {\n	document.getElementById(\"console\").innerHTML = \"Error: Failed to load debug shell\";\n	return;\n}\n\nbrython(1);\n\n})();\n// ]]>\n		</script>\n	</body>\n</html>";
